import request from "../utils/request"; // 获取tree列表

export var getAllTree = function getAllTree(query) {
  return request({
    url: 'get_all_tree',
    method: 'get',
    params: query
  });
}; // 删除tree

export var deleteTree = function deleteTree(query) {
  return request({
    url: 'del_tree',
    method: 'get',
    params: query
  });
}; // 编辑tree

export var editTree = function editTree(query) {
  return request({
    url: 'edit_tree',
    method: 'post',
    data: query
  });
}; // 新建tree

export var addTree = function addTree(query) {
  return request({
    url: 'create_tree',
    method: 'post',
    data: query
  });
};